<template>
  <div @touchmove.stop.prevent class="header">
    <div class="icon">
      <div>
        <img class="logo" :src="logo" alt="logo">
      </div>
      <div @click="dialog">
        <img class="option" :src="choose" alt="操作">
      </div>
    </div>
    <div class="dialog" :class="{slideInDown:show}">
      <div class="dialog-header">
        <div class="close" @click="dialog">
            <span class="option"></span>
          <!-- <img class="option" src alt="操作"> -->
        </div>
        <div>销售咨询： 李震 153 1372 6009</div>
        <div>商务合作： 史宁 186 1270 3341</div>
      </div>
      <div class="dialog-content">
        <p>
          <router-link :to="'/index'">首页</router-link>
        </p>
        <p>
          <router-link :to="'/newRetailProduct'">智慧新零售</router-link>
        </p>
        <p>
          <router-link :to="'/wisdomNewService'">智慧新服务</router-link>
        </p>
        <p>
          <router-link :to="'/aboutUs'">关于彩羽</router-link>
        </p>
        <p>
            <a href="https://www.ifengniao.com">蜂鸟创新</a>
          <!-- <router-link :to="''"></router-link> -->
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "headers",
  data() {
    return {
      show: false,
      choose: require("../assets/images/header/icon_choose@2x.png"),
      logo: require("../assets/images/header/logo@2x.png")
    };
  },
  methods: {
    dialog() {
      this.show = !this.show;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.header {
  padding: 0.25rem 0.32rem 0 0.4rem;
  color: #ffffff;
}

.icon {
  display: flex;
  justify-content: space-between;
  .logo {
    width: 3.916rem;
  }
  .option {
    width: 0.36rem;
  }
}
.dialog {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #333333;
  padding: 0.26rem 0.32rem 0 0.54rem;
  display: none;
  .dialog-header {
    position: relative;
    font-size: 0.26rem;
    letter-spacing: 0;
    > div {
      margin-bottom: 0.08rem;
    }
  }
  .dialog-content {
    margin-top: 1.82rem;
    text-align: center;
    p {
      font-size: 0.48rem;
      margin-bottom: 0.86rem;
    }
    p:last-child {
      margin-bottom: 0;
    }
  }
  .close {
    position: absolute;
    right: 0;
    .option {
        display: inline-block;
      width: 0.36rem;
      height: .34rem;
      background:url(data:image/png;base64,) no-repeat;
      background-size: 100%;
    }
  }
}

.slideInDown {
  -webkit-animation-name: slideInDown;
  animation-name: slideInDown;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  display: block;
}

@-webkit-keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

// .slideOutUp {
//   animation-duration: 0.5s;
//   animation-fill-mode: both;
//   -webkit-animation-name: slideOutUp;
//   animation-name: slideOutUp;
//   display: none;
// }
// @-webkit-keyframes slideOutUp {
//   from {
//     -webkit-transform: translate3d(0, 0, 0);
//     transform: translate3d(0, 0, 0);
//   }

//   to {
//     visibility: hidden;
//     -webkit-transform: translate3d(0, -100%, 0);
//     transform: translate3d(0, -100%, 0);
//   }
// }

// @keyframes slideOutUp {
//   from {
//     -webkit-transform: translate3d(0, 0, 0);
//     transform: translate3d(0, 0, 0);
//   }

//   to {
//     visibility: hidden;
//     -webkit-transform: translate3d(0, -100%, 0);
//     transform: translate3d(0, -100%, 0);
//   }
// }
</style>



