import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Index from '@/page/index'
// import AboutUs from '@/page/aboutUs'
// import WisdomNewService from '@/page/wisdomNewService'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/aboutUs',
      component: resolve => require(['@/page/aboutUs'], resolve)
    },
    {
      path: '/wisdomNewService',
      component: resolve => require(['@/page/wisdomNewService'], resolve)
    },
    {
      path: '/newRetailCusService',
      component: resolve => require(['@/page/newRetailCusService'], resolve)
    },
    {
        path: '/newRetailProduct',
        component: resolve => require(['@/page/newRetailProduct'], resolve)
    },
    {
        path: '/caseDetail',
        component: resolve => require(['@/page/caseDetail'], resolve)
    }
  ]
})



